body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  .sidebar {
    width: 250px;
    height: 100vh;
    background-color: rgb(210, 168, 250);
    padding: 20px;
    display: flex;
    flex-direction: column;
  }
  
  .greeting {
    display: flex;
    margin-top: 5px;
  }
  
  .greeting-text {
    font-size: 16px;
    text-align: center;
  }
  .profile-pic {
    width: 70px;
    height: 40px;
    border-radius: 50%;
    background-color: black;
    margin-right: 50px;
    margin-top: 5px;
    font-size: 15px;
  }
  
  .tasks p {
    font-size: 14px;
    margin-top: 20%;
  }
  
  .delete-tasks p {
    font-size: 14px;
    color: #f0f0f0;
    margin-top: 100%;
  }
  .all p{
    font-size: 18px;
    color: blueviolet;
    font-weight: bold;
  }
  .tasks p{
    font-size: 18px;
    font-size: 700;
    margin-top: 10%;
  }

  @media(max-width:700px){
    .sidebar {
      width: 170px;
      height: 100%;
      padding: 0px;
      margin-left: 200px;
    }

  } 
  