.bell ion-icon{
    color: #fff;
    padding: 8px 10px;
    font-size: 25px;
    margin-top:25px;
    margin-left: 400px;
}
.form{
    width: 250px;
    height: 230px;
    background: blueviolet;
    margin-bottom: 30px;
    margin-left: 40px;
    border-radius: 10px;
    padding: 10px 10px;
}
.form h2, p{
    width: 220px;
    font-family: 'Times New Roman', Times, serif;
    text-align: left;
    color: white;
    font-size: 15px;
    border-radius: 10px;
    margin: 2px;
    padding: 5px;
    background-color: transparent;
    margin-left: -1px;
}
.can{
    background-color: transparent;
    display: flex;
    margin-top: 10px;
    margin-left: 5px;
}
.can ion-icon{
    color: white;
    padding-top: 5px;
    background-color: transparent;
}
.can h2{
    font-size: 15px;
    padding: 5px;
}
.com{
    width: 100px;
    height: 25px;
    background: palegoldenrod;
    border: none;
    margin-top: 5px;
    margin-left: 5px;
    font-size: 15px;
    font-weight: 500;
    border-radius: 10px;
    cursor: pointer;
    color: black;
    text-decoration: none;
}
.time{
    display: flex;
    margin-left: 5px;
}
.last{
    background-color: transparent;
}
.last ion-icon{
    background-color: transparent;
    color: white;
}

@media(max-width:700px){
.form{
    width: 180px;
    height: 150px;
    margin-bottom: 20px;
    margin-left: 20px;
    border-radius: 10px;
    background: blueviolet;
    padding-top: 0px;
}
.form h2, .form p{
    width: 110px;
    font-size: 12px;
    font-weight: 600;
    padding: 3px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.com{
    width: 80px;
    font-size: 12px;
}
.CalendarOutlined{
    font-size: 10px;
}

}