*{
    margin: 0;
    padding: 0;
    font-family:'Times New Roman', Times, serif;
  }

  .navbar {
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 20px;
    font-family: 'Times New Roman', Times, serif;
    margin-left: -260px;
  }
  
  .search{
    position: relative;
    width: 330px;
    float: left;
    margin-left: 270px;
  }
  
  .search-input {
    padding: 8px 10px;
    border: none;
    border-radius: 7px;
    outline: none;
    width: 200px;
    height: 40px;
    background: #f0f0f0;
    margin-top: 30px;
    color: black;
    font-size: 17px;
    font-weight: 550;
    float: left;
  }


.btn{
    width: 150px;
    height: 40px;
    background: blueviolet;
    border: none;
    margin-top: 15px;
    font-size: 18px;
    border-radius: 10px;
    cursor: pointer;
    color: #fff;
    transition: 0.4s ease;
    text-decoration: none;
}

.btn a{
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    background-color: transparent;
}

.btnn{
  width: 150px;
  height: 40px;
  border: none;
  margin-top: 15px;
  font-size: 18px;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.4s ease;
  text-decoration: none;
}
.btnn a{
  text-decoration: none;
  color: gray;
  font-weight: bold;
  background-color: transparent;
}

@media(max-width:700px){
  
  .search-input {
    width: 60px;
    font-size: 10px;
    height: 20px;
  }

  .btnn{
    width: 120px;
    margin-top: 10px;
    font-size: 10px;
    font-weight: 500;
    height: 20px;
    border-radius: 5px;
  }

  .btn{
    width: 150px;
    margin-top: 10px;
    margin-right: 5px;
    font-size: 12px;
    font-weight: 500;
    height: 30px;
}

  
} 