*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#container{
  display: flex;
  position: relative;
  justify-content: space-between;
  width: 100%;
  height: 100vh;
}

#content{
  width: 100%;
  display: flex;
  flex-direction: column;
}


#task-container{
  padding: 0px 20px;
  padding-top: 80px;
  display: flex;
  flex-wrap: wrap;
  height: max-content;
  overflow: auto;
}

#taskForm{
  max-width: 400px;
  margin: 20px;
  padding: 20PX;
  background-color: #fff;
  padding: 10px;
  border-radius: 5px;
  margin-top: 25px;
  font-family: 'Times New Roman', Times, serif;
  font-weight: bold;
}

div {
  display: block;
  margin-bottom: 5px;
}

input[type="text"],
input[type="date"], textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 2px solid #ccc;
    border-radius: 5px;
}